import i18n from 'i18next';
import {initReactI18next} from 'react-i18next';

i18n
    // load translation using http -> see /public/locales (i.e. https://github.com/i18next/react-i18next/tree/master/example/react/public/locales)
    // learn more: https://github.com/i18next/i18next-http-backend
    .use(initReactI18next)
    // init i18next
    // for all options read: https://www.i18next.com/overview/configuration-options
    .init({
        fallbackLng: 'en',
        load: 'all',
        preload: ['en', 'ru'],
        debug: true,
        interpolation: {
            escapeValue: false, // not needed for react as it escapes by default
        },
        resources: {
            en: {
                translation: {
                    "Common": {
                        "Rovers": "Rovers",
                        "Search": "Search photo",
                        "About": "About",
                        "Profile": "Profile",
                        "Logout": "Logout",
                        "Demo": "Demo mode"
                    },
                    "Rovers": {
                        "Curiosity": {
                            "Name": "Curiosity",
                            "Description": "It's mission began in 2011 and continues now.",
                            "Learn more": "Learn more"
                        },
                        "Opportunity": {
                            "Name": "Opportunity",
                            "Description": "This rover that was active from 2004 until 2018.",
                            "Learn more": "Learn more"
                        },
                        "Spirit": {
                            "Name": "Spirit",
                            "Description": "It's mission lasted from 2004 to 2010",
                            "Learn more": "Learn more"
                        }
                    },
                    Login: {
                        "Name": "Name",
                        "ApiKey": "API key",
                        "Save": "Save authorization data and use it in the future.",
                        "Login": "Login",
                        "ApiKey Info": "This application makes requests to NASA's open API during its operation. " +
                            "To authorize users, a special key is required, which you can get at ",
                        "DemoKey Info":
                            "If for some reason you do not want to register, then you still can use this application, " +
                            "however, the API has a limit on the number of requests and you can only make 25 requests from one IP " +
                            "address per day. You can check the number of remaining requests on the profile page."
                    },
                    "Search": {
                        "End": "End date",
                        "Start": "Start date",
                        "Camera": "Camera",
                        "Search": "Search",
                        "FHAZ": "Front Hazard Avoidance Camera",
                        "RHAZ": "Rear Hazard Avoidance Camera",
                        "MAST": "Mast Camera",
                        "CHEMCAM": "Chemistry and Camera Complex",
                        "MAHLI": "Mars Hand Lens Imager",
                        "MARDI": "Mars Descent Imager",
                        "NAVCAM": "Navigation Camera",
                        "PANCAM": "Panoramic Camera",
                        "MINITES": "Miniature Thermal Emission Spectrometer (Mini-TES)"
                    }
                }
            }, ru: {
                translation: {
                    "Common": {
                        "Rovers": "",
                        "Search": " ",
                        "About": " ",
                        "Profile": "",
                        "Logout": "",
                        "Demo": " "
                    },
                    "Rovers": {
                        Curiosity: {
                            'Name': "Curiosity ()",
                            "Description": "    2011      .",
                            "Learn more": " "
                        },
                        "Opportunity": {
                            "Name": "Opportunity ()",
                            "Description": "     2004  2018 .",
                            "Learn more": " "
                        },
                        "Spirit": {
                            "Name": "Spirit ()",
                            "Description": "    2004  2010 .",
                            "Learn more": " "
                        }
                    },
                    "Login": {
                        "Name": " ",
                        "ApiKey": "API ",
                        "Login": "",
                        "Save": "       .",
                        "ApiKey Info": "           . " +
                            "     ,     " +
                            "  ",
                        "DemoKey Info": "  -     , " +
                            "     , ,      " +
                            "      25    IP   . " +
                            "        ."
                    },
                    "Search": {
                        "End": " ",
                        "Start": " ",
                        "Camera": "",
                        "Search": "",
                        "FHAZ": "     ",
                        "RHAZ": "     ",
                        "MAST": " ",
                        "CHEMCAM": "   ",
                        "MAHLI": "    ",
                        "MARDI": " ",
                        "NAVCAM": " ",
                        "PANCAM": " ",
                        "MINITES": "  "
                    }
                }
            }
        }
    });


export default i18n;
